import React, {PureComponent} from "react";
import PropTypes from "prop-types";
import $ from "jquery";

import {OraInput, OraLabel} from "../../../components/OraCommonComponents";
import ErrorRow from "../../core/components/hoc/ErrorRow"
import OraFloatingLabelWrapper from "./OraFloatingLabelWrapper";


class InputWithFloatingLabel extends PureComponent {
    static propTypes = {
        id: PropTypes.string,
        value: PropTypes.string,
        placeholder: PropTypes.string,
        required: PropTypes.bool,
        disabled: PropTypes.bool,
        autocomplete: PropTypes.bool,
        autocompleteUrl: PropTypes.string,
        onChange: PropTypes.func,
        onSelect: PropTypes.func,
        children: PropTypes.any,
        overrideClass: PropTypes.string,
        showValidation: PropTypes.bool,
        valid: PropTypes.bool,
        showErrors: PropTypes.bool,
        customInputClass: PropTypes.string,
        autoSelect: PropTypes.bool
    };

    static defaultProps = {
        required: false,
        disabled: false,
        autocomplete: false,
        showValidation: false,
        showErrors: false,
        customInputClass: ""
    };

    constructor(props){
        super(props)
        this.state = {
            validationOnMount: !!props.value || (!!props.errors && props.errors.length > 0),
            isFocused:false
        }
    }


    componentWillUpdate(nextProps) {
        if (this.state.validationOnMount) {
            const hasErrors = !!nextProps.errors;
            const hadErrors = !!this.props.errors;
            if (hasErrors ^ hadErrors) {
                this.setState({
                    validationOnMount: false
                })
            } else if (this.props.value !== nextProps.value) {
                this.setState({
                    validationOnMount: false
                })
            }
        }
    }
    onFocus(){
        this.setState({isFocused:true})
    }
    onBlur(event){
        if(this.props.onBlur)
            this.props.onBlur(event);

        this.setState({isFocused:false})

    }
    render() {
        let inputClassName = this.props.customInputClass ? this.props.customInputClass : "";
        let markClassName = 'o-validation-mark';
        const errorsFilled = !!this.props.errors;
        const showValidation = this.props.showValidation && (errorsFilled || this.state.validationOnMount || !!this.props.value)
        if (showValidation ) {
            if (this.props.valid === true) {
                inputClassName += " success";
                markClassName += " o-validation-mark--success "
            } else if (this.props.valid === false && !this.state.isFocused) {
                inputClassName += " error";
                markClassName += "  o-validation-mark--error "
            }
        }

        const showErrorRow = this.props.showErrors && errorsFilled;
        return (
            <ErrorRow showErrors={showErrorRow} errors={this.props.errors}>
                <OraFloatingLabelWrapper autoComplete={this.props.autocomplete} maximumWidth={true} overrideClass={this.props.overrideClass}>
                    <OraInput
                        autocomplete={this.props.autocomplete}
                        autocompleteUrl={this.props.autocompleteUrl}
                        ref={ref=> this.ref=ref}
                        type="text"
                        id={this.props.id}
                        value={this.props.value}
                        required={this.props.required}
                        onChange={this.props.onChange}
                        disabled={this.props.autoSelect || this.props.disabled}
                        onBlur={this.onBlur.bind(this)}
                        className={inputClassName}
                        onFocus={this.onFocus.bind(this)}
                        focusOnMount={this.props.focusOnMount}
                        onPick={this.props.onPick}
                        browserAutocomplete={this.props.browserAutocomplete || (this.props.id +"no_autocomplete")}
                        name={this.props.name || (this.props.id + "_no_autocomplete")}
                        onKeyUp={this.props.onKeyUp}
                    />
                    {this.props.showValidation && <span className={markClassName} />}
                    <OraLabel
                        htmlFor={this.props.id}
                        className="label o-floating-label__placeholder o-floating-label__p-2"
                    >
                        {this.props.placeholder}
                    </OraLabel>
                    {this.props.children}
                </OraFloatingLabelWrapper>
            </ErrorRow>
        );
    }
}

export default InputWithFloatingLabel;